
@media (max-width: 1200px) { 
  .achievements-digit, .achievements-text {
    justify-content: center; /* Центрируем только на маленьких экранах */
		display: flex;
  }
}
@media (max-width: 576px) { 
  .achievements-digit, .achievements-text {
    justify-content: center; /* Центрируем только на маленьких экранах */
		text-align: center !important;
  }
}




.aboutSchool {
	/* height: 1100px; */
	margin-top: 105px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.main-info{
	font-family: Jost;
	font-size: 30px;
	font-weight: 600;
	line-height: 48px;
	text-align: left;
	text-underline-position: from-font;
	text-decoration-skip-ink: none;
}
.main-info span{
	font-family: Jost;
	font-size: 30px;
	font-weight: 400;
	line-height: 48px;
	text-align: left;
	text-underline-position: from-font;
	text-decoration-skip-ink: none;
}

.achievements{
	display: flex	;

	/* flex-direction: column; */
	justify-content: center;
	margin-bottom: 100px;
	
}
.achievements-digit{
	font-weight: 600;
	font-size:70px;
	line-height: 112px;
	color: var(--darkred);

	/* width: 80px;
	height: 112px; */

}
.achievements-text{
	font-family: Jost;
font-size: 18px;
font-weight: 400;
line-height: 28.8px;
text-align: left;
text-underline-position: from-font;
text-decoration-skip-ink: none;
color: var(--lightblack);
}